/* Mobile Responsive */
.header-mobile-responsive {
  text-align: end;
  font-family: "Patrick Hand";
  font-size: 20px;
  background-color: #4a6c6f;
}
.header-mobile-margin {
  margin-right: auto !important;
}
.header-mobile-route {
  color: white !important;
  padding: 5px 10px !important;
}
.header-mobile-route:hover {
  color: #c0bcb5 !important;
  text-decoration: none;
}
@media (min-width: 672px) {
  .header-mobile-responsive {
    content-visibility: hidden;
    padding: 0px !important;
  }
}

/* Desktop Header */
.header-desktop-responsive {
  background-color: #4a6c6f;
}
.header-desktop-nav {
  width: 100%;
  place-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Patrick Hand";
  font-size: 1.7rem;
  color: white;
}
.header-desktop-route {
  color: white !important;
  padding: 0px 25px !important;
}
.header-desktop-route:hover {
  color: #c0bcb5 !important;
  text-decoration: none;
}
@media (max-width: 672px) {
  .header-desktop-responsive {
    content-visibility: hidden;
    padding: 0px !important;
  }
}

.home-header {
  text-align: center;
  padding-bottom: 20px;
}
.home-posters {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.home-deck {
  place-content: center;
  text-align: -webkit-center;
  padding-top: 30px;
}
.home-card {
  min-width: 17.5rem !important;
  max-width: 17.5rem !important;
  margin-bottom: 25px !important;
}
.home-image {
  height: 252.4px !important;
  object-fit: cover !important;
}
.home-title {
  text-align: center;
  margin-bottom: 45px;
}
.home-card-title:hover {
  color: #ed474a;
  text-decoration: none;
}
.home-card-price {
  color: #ed474a;
}
.home-card-price:hover {
  color: black;
  text-decoration: none;
}
.home-card-order,
.home-card-order:hover {
  color: black;
}

@media (max-width: 370px) {
  .home-card {
    min-width: 180px !important;
    max-width: 180px !important;
    margin-bottom: 25px !important;
  }
  .home-image {
    height: 180px !important;
  }
  .card-title {
    font-size: inherit !important;
  }
  .card-footer {
    font-size: smaller !important;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .home-card {
    min-width: 144px !important;
    max-width: 144px !important;
  }
  .home-image {
    height: 144px !important;
  }
}
@media (min-width: 768px) and (max-width: 990px) {
  .home-card {
    min-width: 208px !important;
    max-width: 208px !important;
  }
  .home-image {
    height: 208px !important;
  }
}

.about-header {
  text-align: center;
}
.about-par1 {
  padding-top: 30px;
  text-align: center;
}
/* TEAM */
.about-team-name {
  padding-top: 30px;
