}
.product-title {
  text-align: center;
  padding-bottom: 30px;
}
.product-details {
  padding-left: 30px;
  -webkit-align-self: center;
          align-self: center;
}
.product-price-quantity {
  padding-top: 20px;
}
.product-button {
  text-align: center;
  padding-top: 20px;
  -webkit-align-items: center;
          align-items: center;
}

