.notfound-background {
  position: absolute;
  top: 83px;
  left: 0px;
  height: 95%;
  width: 100%;
  z-index: -1;
}
.notfound-div {
  position: absolute;
  top: 20rem;
  bottom: 0;
  left: 0;
  right: 0;

  margin: auto;
}
.notfound-text {
  text-align: center !important;
  vertical-align: middle !important;
}

